/** Media Queries **/
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-size: 1.6rem;
  color: #262728;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.container-sm {
  max-width: 40rem;
  margin: 0 auto;
}

.container-md {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Roboto", sans-serif;
  margin: 0;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
  text-align: center;
}

p {
  font-size: 1.6rem;
  margin: 0;
}

.tittle-app {
  font-weight: 900;
  font-size: 4rem;
}
@media (max-width: 768px) {
  .tittle-app {
    text-align: center;
  }
}

.tagline {
  font-size: 1.8rem;
  font-family: "Roboto", sans-serif;
}
@media (max-width: 768px) {
  .tagline {
    text-align: center;
  }
}

.page-description {
  font-weight: 500;
  font-size: 2.5rem;
}

.form {
  margin-top: 3rem;
}
.form .field {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 3rem;
}
.form .field input {
  padding: 1rem;
  border: none;
  outline: none;
  border-bottom: 0.1rem solid #e1e1e1;
  transition: 0.3s;
}
.form .field input:hover {
  border-bottom: 0.1rem solid #a1a1a1;
}

.actions {
  margin-top: 5rem;
}
.actions__link {
  margin-bottom: 2rem;
}
.actions__link:last-child {
  margin-bottom: 0;
}
.actions a {
  color: #717171;
  transition: 0.3s;
}

.button-input {
  border: none;
  display: "block";
  padding: 1.5rem 2rem;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  background-color: #ad68d8;
  color: #FFFFFF;
  transition: 0.3s;
  width: 100%;
  border-radius: 0.5rem;
}
.button-input:hover {
  background-color: #973fce;
  cursor: pointer;
}

.my-view {
  background-image: url("../img/background.jpg");
  background-size: cover;
  background-position: center center;
}

.header-login {
  padding: 1rem 5rem;
  background-color: #FFFFFF;
}
@media (max-width: 768px) {
  .header-login {
    padding: 2rem 0;
  }
}
.header-login .tittle-app {
  color: transparent;
  background: linear-gradient(to right, #f495d9 0%, #ad68d8 100%);
  background-clip: text;
}
@media (min-width: 768px) {
  .header-login .tittle-app {
    max-width: 20rem;
  }
}
@media (max-width: 768px) {
  .header-login .tittle-app {
    max-width: 30rem;
    font-size: 5rem;
    margin: 0 auto;
  }
}

.login-box {
  margin-top: 5rem;
  padding: 3rem 5rem 5rem 5rem;
  background-color: #FFFFFF;
  border-radius: 1rem;
  box-shadow: 0px 0px 35px 0px rgba(0, 0, 0, 0.281);
}
.login-box .button-input {
  border: none;
  display: "block";
  padding: 1.5rem 2rem;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  background-color: #ad68d8;
  color: #FFFFFF;
  transition: 0.3s;
  width: 100%;
  border-radius: 0.5rem;
}
.login-box .button-input:hover {
  background-color: #973fce;
  cursor: pointer;
}
.login-box a:hover {
  color: #ad68d8;
}