@use 'variables'as v;

/** Media Queries **/
@mixin min_phone {
  @media (min-width: v.$telefono) {
    @content;
  }
}

@mixin min_tablet {
  @media (min-width: v.$tablet) {
    @content;
  }
}

@mixin min_desktop {
  @media (min-width: v.$desktop) {
    @content;
  }
}

/** Media Queries **/
@mixin max_phone {
  @media (max-width: v.$telefono) {
    @content;
  }
}

@mixin max_tablet {
  @media (max-width: v.$tablet) {
    @content;
  }
}

@mixin max_desktop {
  @media (max-width: v.$desktop) {
    @content;
  }
}


@mixin button($bgColor, $TextColor, $display) {
  border: none;
  display: $display;
  padding: 1.5rem 2rem;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  background-color: $bgColor;
  color: $TextColor;
  transition: .3s;
  // display: inline-block;

  &:hover {
    background-color: darken($bgColor, 10%);
    cursor: pointer;
  }
}

@mixin grid($columns, $space) {
  display: grid;
  gap: $space;
  grid-template-columns: repeat($columns, 1fr);
}